/* DashboardPage.css */
.dashboard-container {
    max-width: 800px;
    margin: 2rem auto;
    font-family: sans-serif;
  }
  
  h2, h3, h4 {
    margin-bottom: 1rem;
  }
  
  form, .transaction-form, .summary-section {
    margin-bottom: 2rem;
    padding: 1rem;
    border-radius: 8px;
    background-color: #f8f8f8;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  }
  
  .transaction-form input,
  .transaction-form select,
  .transaction-form button,

  .summary-section input,
  .summary-section button {
    margin: 0.5rem 0.5rem 0.5rem 0;
    padding: 0.5rem;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  button {
    background-color: #1976d2;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #125aa0;
  }
  
  ul {
    padding-left: 1.2rem;
  }
  
  li {
    margin-bottom: 0.5rem;
  }
  
  .edit-btn, .delete-btn {
    margin-left: 0.5rem;
    padding: 0.3rem 0.6rem;
    font-size: 0.9rem;
  }
  
  .edit-btn {
    background-color: #ffa000;
  }
  
  .delete-btn {
    background-color: #d32f2f;
  }
  
  .edit-btn:hover {
    background-color: #cc8500;
  }
  
  .delete-btn:hover {
    background-color: #9a0007;
  }
  
  .error-message {
    color: red;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  
  .summary-result {
    white-space: pre-wrap;
    background-color: #eef;
    padding: 1rem;
    border-radius: 6px;
    font-family: monospace;
  }
  .refresh-btn {
    background-color: #007bff;
    color: white;
    padding: 8px 12px;
    margin-bottom: 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  .refresh-btn:hover {
    background-color: #0056b3;
  }
  .transaction-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    font-size: 16px;
  }
  
  .transaction-table th,
  .transaction-table td {
    border: 1px solid #ddd;
    padding: 12px;
    text-align: left;
  }
  
  .transaction-table th {
    background-color: #f0f0f0;
    font-weight: bold;
  }
  
  .transaction-table tr:nth-child(even) {
    background-color: #fafafa;
  }
  
  .type-income {
    color: green;
    font-weight: bold;
  }
  
  .type-expense {
    color: red;
    font-weight: bold;
  }
  
  .edit-btn {
    margin-right: 8px;
    padding: 6px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  .delete-btn {
    margin-right: 8px;
    padding: 6px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .edit-btn {
    background-color: #007bff;
    color: white;
  }
  
  .delete-btn {
    background-color: #dc3545;
    color: white;
  }

  .transaction-form {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start; /* Aligns all fields to the top */
    gap: 0.5rem;
  }
  
  .transaction-form input,
  .transaction-form select,
  .transaction-form textarea,
  .transaction-form button {
    margin: 0;
    padding: 6px 10px;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    cursor: pointer;
  }
  
  .transaction-form textarea {
    height: 48px; /* Match height of input fields */
  }
    
  
  